.auth-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: auto;
  margin-top: 40px;
}

form {
  width: 100%;
}
  
fieldset {
  border: none;
  margin: 0;
  padding: 0;
}
  
legend {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #333;
  font-weight: bold;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #555;
}
  
input[type="number"] {
  width: 100%;
  padding: 8px;
  margin-bottom: 11px;
  margin-top: 7px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}
  
input[type="submit"] {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
  
input[type="submit"]:hover {
  background-color: #0056b3;
}
  
.TimeDisplay {
  margin-top: 20px;
  font-size: 1.2em;
  color: #333;
}
